import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Accordion, AccordionDetails, AccordionSummary, Grid, TextField, Typography, Box, Chip } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import { InputLabel, MenuItem, Select, FormControl } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { getAgentNumber } from 'api/api';
import QualityReportDateFilter from './DateFilter';
import dateFormat from 'dateformat';

export default function QualityFilters({data,setFiltered}) {

  const [agentNo,setAgentNo] = useState("");
  const [agentName,setAgentName] = useState([]);
  const [score,setScore] = useState("");
  const [agents,setAgents] = useState([]);
  //tarihler başlangıç-bitiş
  const [startTimeApi,setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0),'yyyy-mm-dd HH:MM:ss'));
  const [endTimeApi,setEndTimeApi] = useState(dateFormat(new Date().setHours(23, 59, 59),'yyyy-mm-dd HH:MM:ss'));
  useState(()=>{
    getAgentNumber().then(resp => setAgents(JSON.parse(resp.data.data)));
  },[]);
  let [filtersOpen, setFiltersOpen] = useState(true);
  useEffect(()=>{
  var filtered = [...data];
  if(agentNo !== ""){
    filtered = filtered.filter((item) => {
      return Object.keys(item).some(() => {
          return item.ani
              .toString()
              .toLowerCase()
              .includes(agentNo.toLowerCase());
      })
  });
  }
  if(agentName.length > 0){
    filtered = filtered.filter((item) => {
     return agentName.includes(item.agent);
  });
  }
  if(score !== ""){
    filtered = filtered.filter((item) => {
      return Object.keys(item).some(() => {
          return item.score
              .toString()
              .toLowerCase()
              .includes(score.toLowerCase());
      })
  });
  }
  filtered = filtered.filter((item)=>{
    return new Date(item.timeofinsertion).getTime() <= new Date(endTimeApi).getTime() &&
     new Date(item.timeofinsertion).getTime() >= new Date(startTimeApi).getTime();
  })
  if(agentName === "" && agentNo === "" && score === ""){
    setFiltered(data)
  }
  else{
    setFiltered(filtered);
  }
  },[agentNo,agentName,score,data,setFiltered,startTimeApi,endTimeApi]);

  return (
    <div style={{ margin: '20px 0' }}>
      <QualityReportDateFilter setStartTimeApi={setStartTimeApi} setEndTimeApi={setEndTimeApi}/>
      <Accordion expanded={filtersOpen}>
        <AccordionSummary expandIcon={<ExpandMore />} onClick={() => setFiltersOpen(!filtersOpen)}>
          <Typography style={{ marginLeft: '10px' }}>
            <IntlMessages id={'filters'} />
          </Typography>
        </AccordionSummary>
        <AccordionDetails style={{ paddingRight: '20px', paddingLeft: '20px' }}>
          <GridContainer>
            <Grid item md={4}>
              <div style={{ color: '#064635' }}>
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'agent.number'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <TextField
                id='agentNo'
                type="text"
                name="agentNo"
                value={agentNo}
                placeholder="Agent Number"
                onChange={(e) =>{setAgentNo(e.target.value)}}
                />
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <div style={{ color: '#064635' }}>
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'agent.name'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl variant='standard' fullWidth style={{ marginTop: '8px' }}>
              <InputLabel id="agentName">
                  <IntlMessages id={'agent.name'} />
                </InputLabel>
                <Select
                multiple
                  labelId="agentName"
                  label={<IntlMessages id={'agent.name'} />}
                  value={agentName}
                  MenuProps={{ style: { maxHeight:300 } }}
                  renderValue={(selected) => (
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                      {selected.map((value) => (
                        <Chip key={value} label={value} />
                      ))}
                    </Box>
                  )}
                  onChange={(e) => {
                    const {target:{value}} = e;
                    if(value[value.length-1] === ""){
                      setAgentName([]);
                    }
                    else{
                      setAgentName(typeof value === 'string' ? value.split(','):value)
                    }
                  }}>
                  <MenuItem value=""><IntlMessages id={'CallLog.filter.all'} /></MenuItem>
                  {agents && agents.map((item,index)=>(
                  <MenuItem key={index} value={item.dn}>
                    {item.dn + '-' + item.display_name}
                  </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={4}>
              <div style={{ color: '#064635' }}>
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'score'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl  variant="standard" fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="score">
                  <IntlMessages id={'score'} />
                </InputLabel>
                <Select
                  labelId="score"
                  label={<IntlMessages id={'score'} />}
                  value={score}
                  onChange={(e) => setScore(e.target.value)}>
                  <MenuItem value=""><IntlMessages id={'CallLog.filter.all'} /></MenuItem>
                  <MenuItem value="5">5</MenuItem>
                  <MenuItem value="4">4</MenuItem>
                  <MenuItem value="3">3</MenuItem>
                  <MenuItem value="2">2</MenuItem>
                  <MenuItem value="1">1</MenuItem>
                  <MenuItem value="0">0</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </GridContainer>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
