import React, { useState,useEffect } from 'react';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Filter from './Filter';
import Table from './Table';
import GridContainer from '@jumbo/components/GridContainer';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import { Grid } from '@material-ui/core';
import { Alert, Stack } from '@mui/material';
import dateFormat from 'dateformat';

export default function AbondenedCalls() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'abondenedcalls'} />, isActive: true },
  ];
  let [queue,setQueue] = useState('')
  const [color, setColor] = useState('');
  let [params, setParams] = useState({
    startDate: dateFormat(new Date(),'yyyy-mm-dd'),
    endDate: dateFormat(new Date(),'yyyy-mm-dd'),
  });

  const [startDate, setStartDate] = useState(dateFormat(new Date(),'yyyy-mm-dd'));
  const [endDate, setEndDate] = useState(dateFormat(new Date(),'yyyy-mm-dd'));
  useEffect(()=>{
    setParams({
      startDate: dateFormat(startDate,'yyyy-mm-dd'), 
      endDate: dateFormat(endDate,'yyyy-mm-dd')
    })
  },[startDate,endDate])
  return (
    <PageContainer heading={<IntlMessages id={'abondenedcalls'} />} breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12}>
          <Filter setStartDate={setStartDate} setEndDate={setEndDate} setColor={setColor} color={color} queue={queue} setQueue={setQueue} />
        </Grid>
        <Grid item xs={12}>
          <Table params={params} color={color} queue={queue}/>
        </Grid>
      </GridContainer>
      <div style={{ marginTop: '10px' }}>
        <Stack sx={{ width: '100%' }} spacing={2}>
          <Alert severity="info">
            <IntlMessages id={'abondenedcalls.info.red'} />
          </Alert>
          <Alert severity="info">
            <IntlMessages id={'abondenedcalls.info.green'} />
          </Alert>
        </Stack>
      </div>
    </PageContainer>
  );
}
