import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TablePagination from '@material-ui/core/TablePagination';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button, TableBody } from '@material-ui/core';
import { AccessTime, CloudDownload, ContactPhone, Phone, PhoneInTalk, PhoneMissed } from '@material-ui/icons';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { useIntl } from 'react-intl';
import { DataGrid } from '@mui/x-data-grid';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
import { localeText } from '@jumbo/utils/DataGridLocales';

export default function DataTable({ response, setLimit, limit, setOffset, setPage, page, count, setResponse, offset }) {
  const useStyles = makeStyles(theme => ({
    root: {
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    table: {
      minWidth: 650,
    },
  }));

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setPage(0);
  };
  const column = [
    { id: 'did_number', headerName: <IntlMessages id={'did.number'} />, width: '60px' },
    { id: 'total_did', headerName: <IntlMessages id={'wallboard.total.call'} />, width: '100px' },
    { id: 'did_not_answered', headerName: <IntlMessages id={'wallboard.unanswered'} />, width: '30px' },
    { id: 'did_answered', headerName: <IntlMessages id={'wallboard.answered'} />, width: '30px' },
    { id: 'duration', headerName: <IntlMessages id={'Duration'} />, width: '30px' },
    { id: 'act', headerName: <IntlMessages id={'average'} />, width: '30px' },
  ];

  const classes = useStyles();

  let total = {
    total_did_number: 0,
    total_did: 0,
    did_not_answered: 0,
    did_answered: 0,
    duration: 0,
  };

  for (let i = 0; i < response.length; i++) {
    total.total_did_number++;
    total.total_did += parseInt(response[i].total_did);
    total.did_not_answered += parseInt(response[i].did_not_answered);
    total.did_answered += parseInt(response[i].answered);
    total.duration += parseInt(hourToSecond(response[i].duration));
  }
  // response.map(resp => {
  //   total.total_did_number++;
  //   total.total_did += parseInt(resp.total_did);
  //   total.did_not_answered += parseInt(resp.did_not_answered);
  //   total.did_answered += parseInt(resp.answered);
  //   total.duration += parseInt(hourToSecond(resp.duration));
  // });
  function hourToSecond(param) {
    var second =
      parseInt(param.split(' ')[0].slice(0, -1)) * 3600 +
      parseInt(param.split(' ')[1].slice(0, -1)) * 60 +
      parseInt(param.split(' ')[2].slice(0, -1));
    return second;
  }

  function secondToTime(param) {
    let time =
      addZero(Math.floor(param / 60 / 60)) +
      'h ' +
      addZero(Math.floor((param % 3600) / 60)) +
      'm ' +
      addZero(Math.floor((param % 3600) % 60)) +
      's';
    return time;
  }

  function addZero(param) {
    if (param < 10) {
      param = '0' + param;
    }
    return param;
  }

  let currentLanguage = useIntl();

  let dataGridRows = response.map((item, index) => ({
    num: index + 1 + offset,
    id: index,
    did_number: item.did_number,
    total_did: parseInt(item.total_did),
    did_not_answered: parseInt(item.did_not_answered),
    answered: parseInt(item.answered),
    duration: item.duration,
    act: item.act,
  }));

  const columns = [
    {
      field: 'num',
      headerName: '#',
      renderCell: params => <b>{params.value}</b>,
    },
    {
      field: 'did_number',
      headerName: <IntlMessages id={'didnumber'} />,
      width: 200,
      renderCell: params => (
        <span>
          <ContactPhone style={{ verticalAlign: '-5px', color: '#406882' }} />{' '}
          <b style={{ fontFamily: 'Roboto' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'total_did',
      headerName: <IntlMessages id={'totalcallcount'} />,
      width: 200,
      renderCell: params => (
        <span>
          <Phone style={{ verticalAlign: '-5px', color: '#6998AB' }} />{' '}
          <b style={{ fontFamily: 'Roboto', color: '#6998AB' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'did_not_answered',
      headerName: <IntlMessages id={'unansweredcallcount'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PhoneMissed style={{ verticalAlign: '-5px', color: '#F05454' }} />{' '}
          <b style={{ fontFamily: 'Roboto', color: '#F05454' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'answered',
      headerName: <IntlMessages id={'answeredcallcount'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PhoneInTalk style={{ verticalAlign: '-5px', color: '#519259' }} />{' '}
          <b style={{ fontFamily: 'Roboto', color: '#519259' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'duration',
      headerName: <IntlMessages id={'duration'} />,
      width: 200,
      renderCell: params => (
        <span>
          <AccessTime style={{ verticalAlign: '-5px', color: '#D47AE8' }} />{' '}
          <b style={{ fontFamily: 'Roboto' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'act',
      headerName: <IntlMessages id={'average'} />,
      width: 200,
      renderCell: params => (
        <span>
          <AccessTime style={{ verticalAlign: '-5px', color: '#7267CB' }} />{' '}
          <b style={{ fontFamily: 'Roboto' }}>{params.value}</b>
        </span>
      ),
    },
  ];
  let curentLanguage = useIntl();
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDowlandButtonClick = () => {
    setLimit(parseInt(count));
    setPage(0);
    setResponse([]);
    handleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {response.length > 0 && (
          <Button onClick={() => handleDowlandButtonClick()} style={{ backgroundColor: '#3f51b5', color: 'white' }}>
            <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          </Button>
        )}
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description">
          <Box sx={style}>
            {response.length < 1 && (
              <div style={{ textAlign: 'center' }}>
                <CircularProgress />
                <p>Veriler hazirlanıyor...</p>
              </div>
            )}
            <div style={{ textAlign: 'center' }}>
              {response.length < 1 && (
                <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              )}
              {response.length > 0 && (
                <ExcelFile
                  filename={
                    curentLanguage.locale === 'en-EN'
                      ? 'DID_report'
                      : curentLanguage.locale === 'tr-TR'
                      ? 'DID_raporu'
                      : curentLanguage.locale === 'nl-NL'
                      ? 'DID_rapport'
                      : curentLanguage.locale === 'de-DE'
                      ? 'DID_report'
                      : ''
                  }
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={response} name="Data">
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'DID Number'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Did Numarası'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'DID-Nummer'
                          : curentLanguage.locale === 'de-DE'
                          ? 'DID-Nummer'
                          : ''
                      }
                      value="did_number"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Total Call Count'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Toplam Arama Adedi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Totaal aantal gesprekken'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Gesamtzahl der Anrufe'
                          : ''
                      }
                      value="total_did"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Unanswered Call Count'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Cevaplanmamış Çağrı Adedi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Aantal onbeantwoorde oproepen'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Anzahl unbeantworteter Anrufe'
                          : ''
                      }
                      value="did_not_answered"
                    />
                    <ExcelColumn
                      label={
                        curentLanguage.locale === 'en-EN'
                          ? 'Answered Call Count'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Cevaplanmış Çağrı Adedi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Aantal beantwoorde oproepen'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Zählung angenommener Anrufe'
                          : ''
                      }
                      value="answered"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
          </Box>
        </Modal>
        {/* <ExcelFile
            filename={curentLanguage.locale === 'en-US' ? 'DID_report' : 'DID_raporu'}
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={response} name="Data">
              <ExcelColumn label={currentLanguage.locale == 'en-US' ? 'DID Number' : 'Did Numarası'} value="did_number" />
              <ExcelColumn
                label={currentLanguage.locale == 'en-US' ? 'Total Call Count' : 'Toplam Arama Sayısı'}
                value="total_did"
              />
              <ExcelColumn
                label={currentLanguage.locale == 'en-US' ? 'Unanswered Calls' : 'Cevaplanmamış Çağrılar'}
                value="did_not_answered"
              />
              <ExcelColumn
                label={currentLanguage.locale == 'en-US' ? 'Answered Calls' : 'Cevaplanmış Çağrılar'}
                value="answered"
              />
            </ExcelSheet>
          </ExcelFile> */}
      </div>
      <Paper style={{ marginBottom: '15px' }}>
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid rows={dataGridRows} columns={columns} hideFooter localeText={localeText} />
        </div>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={count}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={response.length < limit ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <TableContainer component={Paper} className={classes.root} style={{ fontFamily: 'Roboto' }}>
        <Table className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
          <TableHead>
            <TableRow>
              {column.map(column => (
                <TableCell key={column.id} style={{ width: column.width }}>
                  {column.headerName}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {response.length > 0 && (
              <TableRow style={{ backgroundColor: '#80ED99' }}>
                <TableCell>
                  {count + ' '} <IntlMessages id={'did.number'} />
                </TableCell>
                <TableCell>{total.total_did}</TableCell>
                <TableCell>{total.did_not_answered}</TableCell>
                <TableCell>{total.did_answered}</TableCell>
                <TableCell>{secondToTime(total.duration)}</TableCell>
                <TableCell>{secondToTime(total.duration / total.total_did)}</TableCell>
              </TableRow>
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
