import React, { useEffect, useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { getReturnedDropCall,getQueue,getAgentNumber } from 'api/api';
import DataTable from './Table';
import Filter from './Filter'
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import Alert from '@mui/material/Alert';
const ReturnedDropCall = () => {
  const [response, setResponse] = useState([]);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [queue,setQueue] = useState('')
  const [agent,setAgent] = useState('')



  useEffect(() => {
    getReturnedDropCall(limit, offset,agent,queue).then((response) =>{
       var respList = JSON.parse(response.data.data)
       getQueue().then(resp=>{
        var list = JSON.parse(resp.data.data);
        list.forEach(que => {
        for(var i =0;i<respList.length;i++) {      
          if(respList[i].q_num === que.dn) {
            respList[i].q_num = que.dn + " - " + que.display_name
          }
        }
        });
        getAgentNumber().then(resp=>{
          var list = JSON.parse(resp.data.data);
          list.forEach(age => {
          for(var i =0;i<respList.length;i++) {      
            if(respList[i].src_dn === age.dn) {
              respList[i].src_dn = age.dn + " - " + age.display_name
            }
          }
          });
          setResponse(respList) 
        })        
      })     
      });
  }, [limit, offset,agent,queue]);

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'returneddropcall'} />, isActive: true },
  ];

  return (
    <PageContainer heading={<IntlMessages id={'returneddropcall'} />} breadcrumbs={breadcrumbs} >
      <GridContainer>
       <Grid item xs={12}>
       <Filter
          queue={queue}
          setQueue={setQueue}
          agent={agent}
          setAgent={setAgent}
        />
       </Grid>
        <Grid item md={12} lg={12} sm={12}>
          <DataTable
            page={page}
            setPage={setPage}
            response={response}
            limit={limit}
            setLimit={setLimit}
            setOffset={setOffset}
            offset={offset}
            setResponse={setResponse}
          />
        </Grid>
      </GridContainer>

      <Alert style={{ marginTop: '15px' }} severity="info">
        <IntlMessages id={'returneddropcall.info'} />
      </Alert>
    </PageContainer>
  );
};

export default ReturnedDropCall;
