import React from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import { Button } from '@material-ui/core';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { CloudDownload } from '@material-ui/icons';
import { useIntl } from 'react-intl';
const DataTable = ({ response, page, setPage, setLimit, setOffset, offset, limit, setResponse }) => {
  const columns = [
    {
      id: 'num',
      label: '#',
    },
    {
      id: 'dst_start_time',
      label: <IntlMessages id={'filter.menu.startdate'} />,
      minWidth: 60,
      align: 'center',
    },
    {
      id: 'dst_end_time',
      label: <IntlMessages id={'filter.menu.enddate'} />,
      minWidth: 70,
      align: 'center',
    },
    {
      id: 'q_num',
      label: <IntlMessages id={'queue'} />,
      minWidth: 60,
      align: 'center',
    },
    {
      id: 'src_dn',
      label: <IntlMessages id={'internals.summary.agent'} />,
      minWidth: 60,
      align: 'center',
    },

    {
      id: 'dst_dn',
      label: <IntlMessages id={'Called.Numbers'} />,
      minWidth: 60,
      align: 'center',
    },

    {
      id: 'duration',
      label: <IntlMessages id={'Duration'} />,
      minWidth: 60,
      align: 'center',
    },
  ];

  function createData(num, src_dn, src_display_name, dst_start_time, dst_end_time, dst_dn, q_num, duration) {
    return {
      num,
      src_dn,
      src_display_name,
      dst_start_time,
      dst_end_time,
      dst_dn,
      q_num,
      duration,
    };
  }

  const rows = response.map((data, index) =>
    createData(
      index + 1 + offset,
      data.src_dn,
      data.src_display_name,
      data.dst_start_time,
      data.dst_end_time,
      data.dst_dn,
      data.q_num,
      data.duration,
    ),
  );

  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));
  const classes = useStyles();

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  let currentLanguage = useIntl();

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {rows.length > 0 && (
          <ExcelFile
            filename={
              currentLanguage.locale === 'en-EN'
                ? 'Returned_drop_call'
                : currentLanguage.locale === 'tr-TR'
                ? 'Geri_donulmus_kacan_cagrilar'
                : currentLanguage.locale === 'nl-NL'
                ? 'Teruggestuurde drop-call'
                : currentLanguage.locale === 'de-DE'
                ? 'Zurückgegebener Drop-Call'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={rows} name="Data">
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Start Time'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Başlangıç Saati'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Starttijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Startzeit'
                    : ''
                }
                value="dst_start_time"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'End Time'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Bitiş Zamanı'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Eindtijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Endzeit'
                    : ''
                }
                value="dst_end_time"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Queue Number'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Kuyruk Numarası'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Wachtrijnummer'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Warteschlangennummer'
                    : ''
                }
                value="q_num"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Agent Number'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Agent Numarası'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Agent Nummer'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Agentennummer'
                    : ''
                }
                value="src_dn"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Caller Number'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Arayan Numara'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Bellernummer'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Anrufernummer'
                    : ''
                }
                value="dst_dn"
              />
              <ExcelColumn
                label={
                  currentLanguage.locale === 'en-EN'
                    ? 'Duration'
                    : currentLanguage.locale === 'tr-TR'
                    ? 'Süre'
                    : currentLanguage.locale === 'nl-NL'
                    ? 'Looptijd'
                    : currentLanguage.locale === 'de-DE'
                    ? 'Dauer'
                    : ''
                }
                value="duration"
              />
            </ExcelSheet>
          </ExcelFile>
        )}
      </div>
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table" size="small">
            <TableHead>
              <TableRow>
                {columns.map(column => (
                  <TableCell key={column.id} align={column.align} style={{ minWidth: column.minWidth }}>
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row, index) => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                    {columns.map(column => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={(page + 1) * limit}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={response.length < limit || response.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          backIconButtonText={String(rows.length)}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
};

export default DataTable;
/** */
