import React, { useEffect, useState } from 'react';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import * as api from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { Box, Button, CircularProgress, Collapse, Typography } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { getNewDate } from '@jumbo/utils/dateHelper';
import { CloudDownload } from '@material-ui/icons';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
export default function DataTable({ params, color, queue }) {
  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 580,
    },
  }));
  const classes = useStyles();

  // function addZero(param) {
  //   if (param < 10) {
  //     param = '0' + param;
  //   }
  //   return param;
  // }

  const [agents, setAgents] = useState([]);
  const [queues, setQueues] = useState([]);
  useEffect(() => {
    api.getQueue().then(resp => {
      setQueues(JSON.parse(resp.data.data));
    });
    api.getAgentNumber().then(resp => setAgents(JSON.parse(resp.data.data)));
  }, []);
  const columns = [
    { id: 'timeofevent', label: <IntlMessages id={'abondenedcalls.timeofevent'} />, minWidth: 60, align: 'center' },
    { id: 'from_userpart', label: <IntlMessages id={'abondenedcalls.fromuserpart'} />, minWidth: 60, align: 'center' },
    {
      id: 'agent',
      label: <IntlMessages id={'called'} />,
      minWidth: 60,
      align: 'center',
    },
    {
      id: 'queue',
      label: <IntlMessages id={'abondenedcalls.displayname'} />,
      minWidth: 70,
      align: 'center',
    },
    {
      id: 'count',
      label: <IntlMessages id={'abondenedcalls.count'} />,
      minWidth: 60,
      align: 'center',
    },
  ];

  // function checkBetween(startDate, endDate) {
  //   if (Math.round((new Date(endDate).getTime() - new Date(startDate).getTime()) / 1000 / 60 / 60 / 24) <= 30) {
  //     return true;
  //   } else return false;
  // }
  const [response, setResponse] = useState([]);
  const [detail, setDetail] = useState([]);

  let [loading, setLoading] = useState(true);
  useEffect(() => {
    setLoading(true);
    let getDetails = (time, userpart) => {
      return new Promise((resolve, reject) => {
        api.getAbondenedCallDetails(time, userpart).then(resp => resolve(JSON.parse(resp.data.data)));
      });
    };

    api.getAbondenedCalls(params, queue).then(call => {
      let calls = JSON.parse(call.data.data);
      let detailPromises = [];
      calls.forEach(item => detailPromises.push(getDetails(item.time_start, item.from_displayname)));
      Promise.all(detailPromises).then(details => {
        setDetail(details);
        setResponse(calls);
        setLoading(false);
      });
    });
  }, [params, queue]);
  const [open, setOpen] = useState(false);
  let res = [];
  for (let i = 0; i < response.length; i++) {
    res.push({
      from_userpart: response[i].from_displayname,
      timeofevent: getNewDate(response[i].time_start.split(' ')[0]) + ' ' + response[i].time_start.split(' ')[1],
      count: response[i].count,
      queue: queues.find(e => e.dn === response[i].q_num)?.display_name || '',
      agent: agents.find(a => a.dn === response[i].ag_num)?.display_name || '',
    });
  }
  // response.map(resp => {
  //   res.push({
  //     from_userpart: resp.from_userpart,
  //     timeofevent: getNewDate(resp.timeofevent.split(' ')[0]) + ' ' + resp.timeofevent.split(' ')[1],
  //     count: resp.count,
  //     display_name: resp.display_name,
  //   });
  // });
  let curentLanguage = useIntl();

  return (
    <>
      <NotificationContainer />
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '10px' }}>
          <CircularProgress />
        </div>
      )}
      {res.length > 0 && (
        <div style={{ textAlign: 'right', marginBottom: '15px', marginRight: '25px' }}>
          <Button
            style={{ backgroundColor: '#3f51b5', color: 'white', marginRight: '10px' }}
            onClick={() => {
              setOpen(!open);
            }}>
            {open ? <IntlMessages id={'abondenedcalls.hidedetails'} /> : <IntlMessages id={'abondenedcalls.showdetails'} />}
          </Button>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Abondened_calls'
                : curentLanguage.locale === 'tr-TR'
                ? 'Cevaplanmayan_cagrilar'
                : curentLanguage.locale === 'nl-NL'
                ? 'Verlaten oproepen'
                : curentLanguage.locale === 'de-DE'
                ? 'Abgebrochene Anrufe'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={res} name="res">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Time of Event'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Etkinlik Zamanı'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Tijd van evenement'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Zeitpunkt des Ereignisses'
                    : ''
                }
                value="timeofevent"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'From'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Kimden'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Van'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Von'
                    : ''
                }
                value="from_userpart"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Display Name'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Ekran adı'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Weergavenaam'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anzeigename'
                    : ''
                }
                value="display_name"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Count'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Adet'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Graaf'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Anzahl'
                    : ''
                }
                value="count"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="sticky table" size="small">
            <TableHead>
              <TableRow>
                {columns.map((column, index) => (
                  <TableCell key={index} align={column.align} style={{ minWidth: column.minWidth }}>
                    {column.label}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {res.map((row, index) => {
                return detail[index] !== undefined ? (
                  detail[index].length > 0 && (color === 'Green' || color === '') ? (
                    <>
                      <TableRow style={{ backgroundColor: '#4E9F3D' }} hover role="checkbox" tabIndex={-1} key={index}>
                        {columns.map(column => {
                          const value = row[column.id];
                          return (
                            <TableCell key={column.id} align={column.align}>
                              {value}
                            </TableCell>
                          );
                        })}
                      </TableRow>

                      {open && (
                        <TableRow>
                          <TableCell colSpan={5}>
                            <Collapse in={true}>
                              <Box sx={{ margin: 1 }}>
                                <Typography variant="h6" gutterBottom component="div">
                                  <IntlMessages id={'abondenedcalls.detail'} />
                                </Typography>
                                <Table size="medium">
                                  <TableHead>
                                    <TableRow>
                                      <TableCell>
                                        <IntlMessages id={'abondenedcalls.diraction'} />
                                      </TableCell>
                                      <TableCell>
                                        <IntlMessages id={'abondenedcalls.srcdn'} />
                                      </TableCell>
                                      <TableCell>
                                        <IntlMessages id={'abondenedcalls.dststarttime'} />
                                      </TableCell>
                                      <TableCell>
                                        <IntlMessages id={'abondenedcalls.dstendtime'} />
                                      </TableCell>
                                      <TableCell>
                                        <IntlMessages id={'abondenedcalls.time'} />
                                      </TableCell>
                                    </TableRow>
                                  </TableHead>
                                  <TableBody>
                                    {detail[index].map(deta => (
                                      <TableRow>
                                        <TableCell>
                                          {deta.direction === '-->' ? (
                                            <span
                                              style={{
                                                padding: '5px',
                                                backgroundColor: '#4E9F3D',
                                                color: '#125C13',
                                                borderRadius: '10px',
                                                fontWeight: '600',
                                              }}>
                                              <IntlMessages id={'incomingcall'} />
                                            </span>
                                          ) : (
                                            <span
                                              style={{
                                                padding: '5px',
                                                backgroundColor: '#FF7777',
                                                color: '#852747',
                                                borderRadius: '10px',
                                                fontWeight: '600',
                                              }}>
                                              <IntlMessages id={'outgoingcall'} />
                                            </span>
                                          )}
                                        </TableCell>
                                        <TableCell>{deta.src_dn}</TableCell>
                                        <TableCell>{deta.dst_start_time}</TableCell>
                                        <TableCell>{deta.dst_end_time}</TableCell>
                                        <TableCell>{deta.calltime}</TableCell>
                                      </TableRow>
                                    ))}
                                  </TableBody>
                                </Table>
                              </Box>
                            </Collapse>
                          </TableCell>
                        </TableRow>
                      )}
                    </>
                  ) : (
                    (color === 'Red' || color === '') && (
                      <TableRow style={{ backgroundColor: '#FF5C58' }} hover role="checkbox" tabIndex={-1} key={index}>
                        {columns.map(column => {
                          const value = row[column.id];
                          return (
                            <TableCell key={column.id} align={column.align}>
                              {value}
                            </TableCell>
                          );
                        })}
                      </TableRow>
                    )
                  )
                ) : null;
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </>
  );
}
