import React, { useEffect, useState } from 'react';
import * as api from '../../../../api/api';
import { Grid, FormControl, MenuItem, Select } from '@material-ui/core';
import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Logout } from 'services/auth/logout';
import { useHistory } from 'react-router';
import CounterCard from '@jumbo/components/Common/CounterCard';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import AddIcCallIcon from '@mui/icons-material/AddIcCall';
import PhonePausedIcon from '@mui/icons-material/PhonePaused';
import PersonIcon from '@mui/icons-material/Person';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import PhoneForwardedIcon from '@mui/icons-material/PhoneForwarded';
import PhoneCallbackIcon from '@mui/icons-material/PhoneCallback';
import PhoneMissedIcon from '@mui/icons-material/PhoneMissed';
import TimerIcon from '@mui/icons-material/Timer';
import GroupIcon from '@mui/icons-material/Group';
import dateFormat from 'dateformat';
import { DateRange } from '@material-ui/icons';

export default function CallDetail({ queueAmaunt, callAmaunt, agentAmaunt, breakeAgentAmaunt, loginAgentAmaunt }) {
  let history = useHistory();
  const [response, setResponse] = useState([]);

  const [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0), 'yyyy-mm-dd'));
  const [dateFilter, setDateFilter] = useState('daily');
  const [direction, setDirection] = useState('all');

  const handleChangeFilter = e => {
    setDateFilter(e.target.value);
    switch (e.target.value) {
      case 'weekly':
        setStartTimeApi(dateFormat(new Date(new Date().setDate(new Date().getDate() - 7)), 'yyyy-mm-dd'));
        break;
      case 'daily':
        setStartTimeApi(dateFormat(new Date(), 'yyyy-mm-dd'));
        break;
      case 'monthly':
        setStartTimeApi(dateFormat(new Date(new Date().setDate(new Date().getDate() - 30)), 'yyyy-mm-dd'));
        break;
      default:
        setStartTimeApi(dateFormat(new Date(), 'yyyy-mm-dd'));
        break;
    }
  };

  useEffect(() => {
    api
      .getDailyCallDetails(startTimeApi)
      .then(resp => setResponse(JSON.parse(resp.data.data)[0]))
      .catch(err => {
        if (err.response.status === 401) {
          Logout();
          history.push('/signin');
        }
      });
  }, [startTimeApi, history]);
  useEffect(() => {
    const intervalId = setInterval(() => {
      api
        .getDailyCallDetails(startTimeApi)
        .then(resp => setResponse(JSON.parse(resp.data.data)[0]))
        .catch(err => {
          if (err.response.status === 401) {
            Logout();
            history.push('/signin');
          }
        });
    }, 5000);
    return () => clearInterval(intervalId);
  }, [startTimeApi, history]);

  const convertTime = timeString => {
    if (timeString !== undefined) {
      if (timeString.includes('.')) {
        let tempHour = parseInt(timeString.split('.')[0]) * 24;
        return (
          tempHour +
          parseInt(timeString.split('.')[1].split(':')[0]) +
          ':' +
          timeString.split('.')[1].split(':')[1] +
          ':' +
          timeString.split('.')[1].split(':')[2]
        );
      } else {
        return timeString;
      }
    }
  };
  return (
    <div style={{ marginBottom: '1px', fontFamily: 'Roboto' }}>
      <GridContainer>
        <Grid item md={4}>
          <FormControl fullWidth variant="standard">
            <Select value={dateFilter} onChange={handleChangeFilter}>
              <MenuItem value="daily">
                <DateRange />
                <IntlMessages id="daily" />
              </MenuItem>
              <MenuItem value="weekly">
                <DateRange />
                <IntlMessages id="select.menu.weekly" />
              </MenuItem>
              <MenuItem value="monthly">
                <DateRange />
                <IntlMessages id="select.menu.monthly" />
              </MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item md={4}>
          <FormControl fullWidth variant="standard">
            <Select
              value={direction}
              onChange={e => setDirection(e.target.value)}
              label={<IntlMessages id={'direction'} />}>
              <MenuItem value="all">
              <AddIcCallIcon />
                <IntlMessages id={'all'} />
              </MenuItem>
              <MenuItem value="inbound">
              <PhoneCallbackIcon />
                <IntlMessages id={'dashboard.inbound'} />
              </MenuItem>
              <MenuItem value="outbound">
              <PhoneForwardedIcon />
                <IntlMessages id={'dashboard.outbound'} />
              </MenuItem>
            </Select>
          </FormControl>
        </Grid>
      </GridContainer>
      <GridContainer>
        <Grid item md={2} style={{display:direction !== "all" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<AccessTimeIcon />}
              number={
                (response.totaltime !== '' ? convertTime(response.totaltime) : '00:00:00') !== undefined
                  ? (response.totaltime !== '' ? convertTime(response.totaltime) : '00:00:00')
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'
                  : null
              }
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.call.time'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#6166B3 -18.96%', '#544179 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2} style={{display:direction !== "all" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<AddIcCallIcon />}
              number={response.total_call}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.total.call'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#ADCE74 -18.96%', '#4E8D7C 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2} style={{display:direction === "outbound" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhoneCallbackIcon />}
              number={(
                parseInt(response.total_call) -
                parseInt(response.agent_to_agent_calls) -
                parseInt(response.outbound_calls)
              ).toString()}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'dashboard.inbound'} />}
              labelProps={{
                fontSize: 11,
              }}
              backgroundColor={['#FFF9B0  -18.96%', '#F48B29 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2} style={{display:direction === "inbound" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhoneForwardedIcon />}
              number={response.outbound_calls}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'dashboard.outbound'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#8AB6D6  -18.96%', '#2978B5 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2} style={{display:direction === "outbound" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhoneInTalkIcon />}
              number={response.answered_inbound_calls}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.answered'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#EA4C4C  -18.96%', '#C14545  108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}  style={{display:direction === "outbound" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhoneMissedIcon />}
              number={String(
                response.total_call -
                  response.agent_to_agent_calls -
                  response.answered_inbound_calls -
                  response.outbound_calls <
                  0
                  ? '0'
                  : response.total_call -
                      response.agent_to_agent_calls -
                      response.answered_inbound_calls -
                      response.outbound_calls,
              )}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.unanswered'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#F79071 -18.96%', '#FA744F 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhonePausedIcon />}
              number={queueAmaunt.toString()}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.waiting.queue'} />}
              labelProps={{
                fontSize: 11,
              }}
              backgroundColor={['#EA4C4C -18.96%', '#C14545  108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PhoneInTalkIcon />}
              number={callAmaunt.toString()}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.staff.call'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#FFF9B0  -18.96%', '#F48B29 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<PersonIcon />}
              number={agentAmaunt.toString()}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.staff'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#8AB6D6  -18.96%', '#2978B5 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<TimerIcon />}
              number={breakeAgentAmaunt.toString()}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.staff.inbreak'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#ADCE74 -18.96%', '#4E8D7C 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
        <Grid item md={2}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<GroupIcon />}
              number={String(loginAgentAmaunt - callAmaunt < 0 ? 0 : loginAgentAmaunt - callAmaunt)}
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.staff.available'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#6166B3  -18.96%', '#544179 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>

        <Grid item md={2} style={{display:direction !== "all" ? "none" : "unset"}}>
          <div style={{ boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px' }}>
            <CounterCard
              icon={<AccessTimeIcon />}
              number={
                (response.average !== '' ? response.average : '00:00:00') !== undefined
                  ? (response.average !== '' ? response.average : '00:00:00')
                      .toString()
                      .replace(':', 'h ')
                      .replace(':', 'm ') + 's'
                  : null
              }
              numberProps={{
                fontSize: 25,
              }}
              label={<IntlMessages id={'wallboard.average.time'} />}
              labelProps={{
                fontSize: 13,
              }}
              backgroundColor={['#F79071 -18.96%', '#FA744F 108.17%']}
              gradientDirection="180deg"
            />
          </div>
        </Grid>
      </GridContainer>
    </div>
  );
}
