import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Accordion, AccordionDetails, AccordionSummary, Grid, TextField, Typography } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import { InputLabel, MenuItem, Select, FormControl } from '@mui/material';
import React, { useState } from 'react';
import PhoneForwardedIcon from '@mui/icons-material/PhoneForwarded';
import PhoneCallbackIcon from '@mui/icons-material/PhoneCallback';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import AccessTime from '@mui/icons-material/AccessTime';
import InputAdornment from '@mui/material/InputAdornment';
export default function CallLogtherFilters({
  setSourceApi,
  setDestinationApi,
  setSourceFilterText,
  setDestinationFilterText,
  setAnswerStatus,
  setDuration,
}) {
  let [filtersOpen, setFiltersOpen] = useState(true);

  let [sourceSelect, setSourceSelect] = useState('');
  const handleChangeSourceSelect = event => {
    setSourceSelect(event.target.value);
    if (event.target.value === 'all') {
      setSourceApi('');
    } else if (event.target.value === 'out') {
      setSourceApi('1');
    } else if (event.target.value === 'inb') {
      setSourceApi('0');
    }
  };
  let [sourceFilterSelect, setSourceFilterSelect] = useState('');
  let [sourceFilterTextValue, setSourceFilterTextValue] = useState('');
  const handleChangeSourceFilterSelect = event => {
    setSourceFilterSelect(event.target.value);
    setSourceFilterText(event.target.value);
    setSourceFilterTextValue('');
  };
  const handleChangeSourceFilterText = event => {
    setSourceFilterText(sourceFilterSelect + '====' + event.target.value);
    setSourceFilterTextValue(event.target.value);
  };
  let [targetSelect, setTargetSelect] = useState('');
  const handleChangeTargetSelect = event => {
    setTargetSelect(event.target.value);
    if (event.target.value === 'all') {
      setDestinationApi('');
    } else if (event.target.value === 'out') {
      setDestinationApi('1');
    } else if (event.target.value === 'inb') {
      setDestinationApi('0');
    }
  };
  let [targetFilterSelect, setTargetFilterSelect] = useState('');
  let [targetFilterTextValue, setTargetFilterTextValue] = useState('');
  const handleChangeTargetFilterSelect = event => {
    setTargetFilterSelect(event.target.value);
    setTargetFilterTextValue('');
    setDestinationFilterText(event.target.value);
  };
  const handleChangeDestinationFilterText = event => {
    setDestinationFilterText(targetFilterSelect + '====' + event.target.value);
    setTargetFilterTextValue(event.target.value);
  };

  let [filter3Select, setFilter3Select] = useState('');
  const handleChangeFilter3Select = event => {
    setFilter3Select(event.target.value);
    if (event.target.value === 'all') {
      setAnswerStatus('');
    } else if (event.target.value === 'answered') {
      setAnswerStatus('-');
    } else if (event.target.value === 'unanswered') {
      setAnswerStatus('~');
    }
  };
  let [durationSelect, setDurationSelect] = useState('');
  let [durationMinMax, setDurationMinMax] = useState({ durationMin: '', durationMax: '' });
  const handleDurationSelect = e => {
    setDurationSelect(e.target.value);
    e.target.value === 'all'
      ? setDuration('')
      : e.target.value === 'spec'
      ? setDuration(durationMinMax)
      : setDuration(e.target.value);
  };
  const handleDurationChange = e => {
    const { name, value } = e.target;
    if(/^\d*$/.test(value)){
      setDurationMinMax({
        ...durationMinMax,
        [name]: value,
      });
      setDuration({
        ...durationMinMax,
        [name]:value
      })
    }
  };
  return (
    <div style={{ marginTop: '20px' }}>
      <Accordion expanded={filtersOpen}>
        <AccordionSummary expandIcon={<ExpandMore />} onClick={() => setFiltersOpen(!filtersOpen)}>
          <Typography>
            <IntlMessages id={'filters'} />
          </Typography>
        </AccordionSummary>
        <AccordionDetails style={{ paddingRight: '20px', paddingLeft: '20px' }}>
          <GridContainer>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <PhoneForwardedIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'caller'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.source'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={sourceSelect}
                  label={<IntlMessages id={'CallLog.filter.source'} />}
                  onChange={handleChangeSourceSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'out'}>
                    <IntlMessages id={'CallLog.filter.outnumber'} />
                  </MenuItem>
                  <MenuItem value={'inb'}>
                    <IntlMessages id={'CallLog.filter.innumber'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <FilterListIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.sorcefilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.sorcefilter'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={sourceFilterSelect}
                  label={<IntlMessages id={'CallLog.filter.sorcefilter'} />}
                  onChange={handleChangeSourceFilterSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'dnOrDns'}>
                    <IntlMessages id={'CallLog.filter.internalorinternals'} />
                  </MenuItem>
                  <MenuItem value={'numberfilter'}>
                    <IntlMessages id={'CallLog.filter.numberfilter'} />
                  </MenuItem>
                </Select>
              </FormControl>
              {(sourceFilterSelect === 'dnOrDns' || sourceFilterSelect === 'numberfilter') && (
                <div style={{ marginTop: '5px' }}>
                  <TextField
                    style={{ width: '100%' }}
                    label={
                      sourceFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.internalorinternals'} />
                      ) : sourceFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.numberfilter'} />
                      ) : (
                        '-'
                      )
                    }
                    helperText={
                      sourceFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.ep.did'} />
                      ) : sourceFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.ep.number'} />
                      ) : (
                        '-'
                      )
                    }
                    variant="outlined"
                    onChange={handleChangeSourceFilterText}
                    value={sourceFilterTextValue}
                  />
                </div>
              )}
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <PhoneCallbackIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.destination'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.destination'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={targetSelect}
                  label={<IntlMessages id={'CallLog.filter.destination'} />}
                  onChange={handleChangeTargetSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'out'}>
                    <IntlMessages id={'CallLog.filter.outnumber'} />
                  </MenuItem>
                  <MenuItem value={'inb'}>
                    <IntlMessages id={'CallLog.filter.innumber'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <FilterListIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.destinationfilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.destinationfilter'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={targetFilterSelect}
                  label={<IntlMessages id={'CallLog.filter.destinationfilter'} />}
                  onChange={handleChangeTargetFilterSelect}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'dnOrDns'}>
                    <IntlMessages id={'CallLog.filter.internalorinternals'} />
                  </MenuItem>
                  <MenuItem value={'numberfilter'}>
                    <IntlMessages id={'CallLog.filter.numberfilter'} />
                  </MenuItem>
                </Select>
              </FormControl>
              {(targetFilterSelect === 'dnOrDns' || targetFilterSelect === 'numberfilter') && (
                <div style={{ marginTop: '5px' }}>
                  <TextField
                    style={{ width: '100%' }}
                    label={
                      targetFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.internalorinternals'} />
                      ) : targetFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.numberfilter'} />
                      ) : (
                        '-'
                      )
                    }
                    helperText={
                      targetFilterSelect === 'dnOrDns' ? (
                        <IntlMessages id={'CallLog.filter.ep.did'} />
                      ) : targetFilterSelect === 'numberfilter' ? (
                        <IntlMessages id={'CallLog.filter.ep.number'} />
                      ) : (
                        '-'
                      )
                    }
                    variant="outlined"
                    onChange={handleChangeDestinationFilterText}
                    value={targetFilterTextValue}
                  />
                </div>
              )}
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <FilterAltIcon style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'CallLog.filter.filter3'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="source-l">
                  <IntlMessages id={'CallLog.filter.filter3'} />
                </InputLabel>
                <Select
                  labelId="source-l"
                  id="demo-simple-select"
                  value={filter3Select}
                  label={<IntlMessages id={'CallLog.filter.filter3'} />}
                  onChange={handleChangeFilter3Select}>
                  <MenuItem value={'all'}>
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value={'answered'}>
                    <IntlMessages id={'CallLog.filter.answered'} />
                  </MenuItem>
                  <MenuItem value={'unanswered'}>
                    <IntlMessages id={'CallLog.filter.unanswered'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={2}>
              <div style={{ color: '#064635' }}>
                <AccessTime style={{ verticalAlign: '-5px' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'durationfilter'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <FormControl fullWidth style={{ marginTop: '8px' }}>
                <InputLabel id="durationLbl">
                  <IntlMessages id={'durationfilter'} />
                </InputLabel>
                <Select
                  labelId="durationLbl"
                  id="duration-simple-select"
                  value={durationSelect}
                  label={<IntlMessages id={'durationfilter'} />}
                  onChange={handleDurationSelect}>
                  <MenuItem value="all">
                    <IntlMessages id={'CallLog.filter.all'} />
                  </MenuItem>
                  <MenuItem value="3">
                    3 <IntlMessages id={'seconds'} />
                  </MenuItem>
                  <MenuItem value="5">
                    5 <IntlMessages id={'seconds'} />
                  </MenuItem>
                  <MenuItem value="10">
                    10 <IntlMessages id={'seconds'} />
                  </MenuItem>
                  <MenuItem value="20">
                    20 <IntlMessages id={'seconds'} />
                  </MenuItem>
                  <MenuItem value="spec">
                    <IntlMessages id={'custom'} />
                  </MenuItem>
                </Select>
              </FormControl>
              {durationSelect === 'spec' && (
                <>
                  <FormControl sx={{ mt: 2 }}>
                    <TextField
                      label="Min"
                      variant="outlined"
                      name="durationMin"
                      onChange={handleDurationChange}
                      value={durationMinMax.durationMin}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IntlMessages id={'seconds'} />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </FormControl>
                  <FormControl sx={{ mt: 2 }}>
                    <TextField
                      sx={{ mt: 3 }}
                      label="Max"
                      name="durationMax"
                      variant="outlined"
                      onChange={handleDurationChange}
                      value={durationMinMax.durationMax}
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <IntlMessages id={'seconds'} />
                          </InputAdornment>
                        ),
                      }}
                    />
                  </FormControl>
                </>
              )}
            </Grid>
          </GridContainer>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
