import React,{useEffect,useState} from 'react';
import Paper from '@material-ui/core/Paper';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { DataGrid } from '@mui/x-data-grid';
import { useIntl } from 'react-intl';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import { Button } from '@material-ui/core';
import { CloudDownload } from '@material-ui/icons';
import { Switch, Menu, MenuItem } from '@mui/material';
import * as api from 'api/api';
import { localeText } from '@jumbo/utils/DataGridLocales';

export default function AgentTable({ rows, setReloadValues }) {
  //menu açılması mola seçimi
  const [value, setValue] = useState([]);
  const [anchorEl,setAnchorEl] = useState(null);
  const [agentId,setAgentId] = useState("");
  useEffect(() => {
    api.getBreakNums().then(resp => setValue([...JSON.parse(resp.data)]));
  }, []);

  const handleChangeRecordAuth = (event, agentId) => {
    api.setAgentsRecordListenAuthByAgentId(agentId, event.target.checked).then(res => {
      setReloadValues(agentId + event + new Date().getTime());
    });
  };
 const handleChangeQueueAuth = (event, agentId) => {
    // api.setAgentsRecordListenAuthByAgentId(agentId, event.target.checked).then(res => {
    //   setReloadValues(agentId + event + new Date().getTime());
    // });
  };
  const handleChangeStatus = (event,agentId)=>{
    if(!event.target.checked){
      setAgentId(agentId);
      setAnchorEl(event.currentTarget);
    }
    else{
      api.changeAgentStatus(agentId).then(res => {
        setReloadValues(agentId + event + new Date().getTime());
      });
    }
  };
  const handleClose = value => {
    setAnchorEl(null);
    const reg = /^\d+$/;
    if (reg.test(value)) {
      api.changeSessionStatus(agentId).then(resp =>{
        setReloadValues(agentId + new Date().getTime());
      });
    }
  };
    //mesai sonu seçimi
    const handleEndOfShift = () => {
      setAnchorEl(null);
      api.changeSessionStatus(agentId).then(() => {
        api.qstatus({ agentDn: agentId, token: localStorage.getItem('token') });
        setReloadValues(agentId + new Date().getTime());
      });
    };

  let curentLanguage = useIntl();
  const column = [
    {
      field:'num',
      headerName:"#",
      renderCell:params =>(
        <b>{params.value}</b>
      )
    },
    { field: 'id', headerName: <IntlMessages id={'agentnumber'} />, width: 300 },
    { field: 'displayname', headerName: <IntlMessages id={'agentname'} />, width: 500 },
    { 
      field: 'status', 
      headerName: <IntlMessages id={'stateRow'} />, 
      width: 250,
      renderCell: params =>(
        <span>
          <Switch 
          checked={params.value === "Active" ? true : false} 
          onChange={event => handleChangeStatus(event,params.id)}
          />
        </span>
      ) 
    },
    {
      field: 'recordAuth',
      headerName: <IntlMessages id={'reacordlistenauth'} />,
      width: 300,
      renderCell: params => (
        <span>
          <Switch checked={params.value} onChange={event => handleChangeRecordAuth(event, params.id)} />
        </span>
      ),
    },
    {
      field: 'queueAuth',
      headerName: <IntlMessages id={'agentqueueauth'} />,
      width: 300,
      renderCell: params => (
        <span>
          <Switch checked={params.value} onChange={event => handleChangeQueueAuth(event, params.id)} />
        </span>
      ),
    },
  ];

  return (
    <>
      {rows.length > 0 && (
        <div style={{ marginBottom: '10px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Agent Status'
                : curentLanguage.locale === 'tr-TR'
                ? 'Agent Durum'
                : curentLanguage.locale === 'nl-NL'
                ? 'Agentstatus'
                : curentLanguage.locale === 'de-DE'
                ? 'Agentenstatus'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={rows} name="rows">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Agent Number'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Agent Numarası'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Agent Nummer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Agentennummer'
                    : ''
                }
                value="id"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Agent Name'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Agent Adı'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Naam Agent'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Name des Agenten'
                    : ''
                }
                value="displayname"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Status'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Durum'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Statusagent'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Agentenstatus'
                    : ''
                }
                value="status"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Paper>
      <Menu id="simple-menu" anchorEl={anchorEl} keepMounted open={Boolean(anchorEl)} onClose={handleClose}>
        <MenuItem onClick={() => handleEndOfShift()}>
          <IntlMessages id={'shift.end'} />
        </MenuItem>
        {value.map((item, index) => (
          <MenuItem key={index} onClick={() => handleClose(item.value)}>
            {item.value} {item.name}
          </MenuItem>
        ))}
      </Menu>
        <div style={{ height: 700, width: '100%' }}>
          <DataGrid
            rows={rows}
            columns={column}
            pageSize={100}
            rowsPerPageOptions={[10, 25,50, 100]}
            localeText={localeText}
            componentsProps={{
              pagination: {
                labelRowsPerPage: <IntlMessages id={'rows.per.page'} />,
              },
            }}
          />
        </div>
      </Paper>
    </>
    // <TableContainer component={Paper} className={classes.root}>
    //   <Table className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
    //     <TableHead>
    //       <TableRow>
    //         {column.map(column => (
    //           <TableCell key={column.id} style={{ width: column.width }}>
    //             {column.headerName}
    //           </TableCell>
    //         ))}
    //       </TableRow>
    //     </TableHead>
    //     <TableBody>
    //       {rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row, index) => {
    //         return (
    //           <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
    //             {column.map(column => {
    //               const value = row[column.id];

    //               return <TableCell key={column.id}>{value}</TableCell>;
    //             })}
    //           </TableRow>
    //         );
    //       })}
    //     </TableBody>
    //   </Table>
    //   <TablePagination
    //     rowsPerPageOptions={[10, 20, 50, 100]}
    //     component="div"
    //     count={rows.length}
    //     rowsPerPage={rowsPerPage}
    //     page={page}
    //     onPageChange={handleChangePage}
    //     labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
    //     onRowsPerPageChange={handleChangeRowsPerPage}
    //   />
    // </TableContainer>
  );
}
