import { Button, InputLabel, Select, MenuItem, Stack, OutlinedInput, FormControl } from '@mui/material';
import React, { useState, useEffect } from 'react';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { setTagTypes, getDepartmants } from '../../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';


export default function UpdateTagModal({ selectedTag,setReloadTags, updateHandleClose }) {

  //departmanlar
  const [departments, setDepartments] = useState([]);

  //departmanları getir
  useEffect(() => {
    getDepartmants().then(result => {
      setDepartments(JSON.parse(result.data.data));
    });
  }, []);

  //form bilgileri
  const [depId, setDepId] = useState(selectedTag.department_id);
  const [depName, setDepName] = useState(selectedTag.department);
  const [tagName, setTagName] = useState(selectedTag.tagmassage);

  //selecten department seçilirse
  const handleDepChange = (e) => {
    //value ver texti al id ve name olarak geç
    var departmentName = departments.find(item => item.id === e.target.value).name;
    setDepName(departmentName);
    setDepId(e.target.value)
  }

  //submit edilirse
  const handleSubmit = () => {
    if ((tagName !== undefined || tagName !== '') && (depId !== undefined || depId !== '')) {
        setTagTypes(depName,tagName,depId,selectedTag.tagtypes_id).then(() => {
          NotificationManager.success('Etiket Güncellendi', '', 3000);
          setReloadTags((prev) =>prev+1);
          updateHandleClose();
        });
      }else{
        NotificationManager.error('Veriler doğru formatta değil', '', 3000);
      }
  }
  return (
    <div>
      <NotificationContainer />
      <Stack direction="row" spacing={2}>
        <FormControl fullWidth>
          <InputLabel htmlFor="tag"><IntlMessages id={'tag'} /></InputLabel>
          <OutlinedInput
            id="tag"
            type="text"
            value={tagName}
            onChange={(e) => setTagName(e.target.value)}
            label="Etiket Adı" />
      </FormControl>
      <FormControl fullWidth>
      <InputLabel htmlFor="depNameUpdateLbl"><IntlMessages id={'departmentname'} /></InputLabel>
          <Select
            labelId='depNameUpdateLbl'
            fullWidth
            value={depId}
            onChange={handleDepChange}
          >
            {departments && 
            departments.map((item, index) => (
            <MenuItem key={index} value={item.id}>{item.name}</MenuItem>
            ))}
          </Select>

        </FormControl>
      </Stack>
      <div>
          <Button sx={{ width: '100%',marginTop:'1rem' }} color="warning" variant="contained" onClick={() => handleSubmit()}>
            <IntlMessages id={'update'} />
          </Button>
        </div>
    </div>
  );
}
