import React, { useEffect, useState } from 'react';
import { CircularProgress } from '@material-ui/core';
import { getDailySummary, getDailySummaryTotal } from 'api/api';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import DailySummaryDateFilter from './DateFilter';

export default function DailySummaryReportFilter({ setData, setDataTotal }) {
  // function checkBetween(startDate, endDate) {
  //   if (Math.round((new Date(endDate).getTime() - new Date(startDate).getTime()) / 1000 / 60 / 60 / 24) <= 30) {
  //     return true;
  //   } else return false;
  // }
  const formatDate = date => {
    let d = new Date(date);
    let month = '' + (d.getMonth() + 1);
    let day = '' + d.getDate();
    let year = d.getFullYear();
    let hour = d.getHours();
    let min = d.getMinutes();

    if (month < 10) month = '0' + month;
    if (day < 10) day = '0' + day;
    if (hour < 10) hour = '0' + hour;
    if (min < 10) min = '0' + min;

    return year + '-' + month + '-' + day;
  };

  let [startTime, setStartTime] = useState(formatDate(new Date()));
  let [endTime, setEndTime] = useState(formatDate(new Date()));

  // const handleStartTimeChange = event => {
  //   setStartTime(formatDate(event._d));
  // };
  // const handleEndTimeChange = event => {
  //   setEndTime(formatDate(event._d));
  // };

  let [loading, setLoading] = useState(false);

  useEffect(() => {
        setLoading(true);
        getDailySummary(startTime, endTime).then(response => {
          setData(JSON.parse(response.data.data));
          setLoading(false);
        });

        getDailySummaryTotal(startTime, endTime).then(response => {
          setDataTotal(JSON.parse(response.data.data));
          setLoading(false);
        });
  }, [startTime, endTime, setData, setDataTotal]);


  return (
    <div>
      <NotificationContainer />
      <DailySummaryDateFilter setStartTimeApi={setStartTime} setEndTimeApi={setEndTime} />
      {/* <GridContainer>
            <Grid item md={3}>
              <DatePicker
                style={{ width: '100%' }}
                ampm={false}
                disableFuture
                inputVariant="outlined"
                format="DD-MM-YYYY"
                maxDate={endTime}
                label={<IntlMessages id={'filter.menu.startdate'} />}
                value={startTime}
                onChange={handleStartTimeChange}
              />
            </Grid>
            <Grid item md={3}>
              <DatePicker
                style={{ width: '100%' }}
                ampm={false}
                disableFuture
                inputVariant="outlined"
                minDate={startTime}
                format="DD-MM-YYYY"
                label={<IntlMessages id={'filter.menu.enddate'} />}
                value={endTime}
                onChange={handleEndTimeChange}
              />
            </Grid>
          </GridContainer> */}
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
    </div>
  );
}
