import React, { useState, useEffect } from 'react';
import Paper from '@material-ui/core/Paper';
import TablePagination from '@material-ui/core/TablePagination';
import { getQueueNumber } from 'api/api';
import { Button } from '@material-ui/core';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { CloudDownload } from '@material-ui/icons';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { useIntl } from 'react-intl';
import { getNewDate } from '@jumbo/utils/dateHelper';
import { DataGrid } from '@mui/x-data-grid';
import DateRangeIcon from '@mui/icons-material/DateRange';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PersonIcon from '@mui/icons-material/Person';
import ContactPhoneIcon from '@mui/icons-material/ContactPhone';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import { CircularProgress } from '@mui/material';
import { localeText } from '@jumbo/utils/DataGridLocales';

export default function DataTable({
  data,
  setData,
  rowsPerPage,
  setRowsPerPage,
  page,
  setPage,
  summaryTotal,
  responseCount,
}) {
  const [queueList, setQueueList] = useState([]);
  // const column = [
  //   { id: 'dst_start_time', headerName: <IntlMessages id={'queueanswerreport.date'} />, width: '60px' },
  //   { id: 'waiting_time', headerName: <IntlMessages id={'queueanswerreport.waitingtime'} />, width: '100px' },
  //   { id: 'talking', headerName: <IntlMessages id={'queueanswerreport.talkingtime'} />, width: '30px' },
  //   { id: 'src_display_name', headerName: <IntlMessages id={'queueanswerreport.caller'} />, width: '30px' },
  //   { id: 'dst_dn', headerName: <IntlMessages id={'queueanswerreport.queuenumber'} />, width: '30px' },
  // ];

  useEffect(() => {
    getQueueNumber().then(resp => {
      setQueueList(JSON.parse(resp.data.data));
    });
  }, []);

  // const formatDate = date => {
  //   let d = new Date(date);
  //   let month = '' + (d.getMonth() + 1);
  //   let day = '' + d.getDate();
  //   let year = d.getFullYear();
  //   let hour = d.getHours();
  //   let min = d.getMinutes();

  //   if (month < 10) month = '0' + month;
  //   if (day < 10) day = '0' + day;
  //   if (hour < 10) hour = '0' + hour;
  //   if (min < 10) min = '0' + min;

  //   return year + '-' + month + '-' + day;
  // };

  // function addZero(param) {
  //   if (param < 10) {
  //     param = '0' + param;
  //   }
  //   return param;
  // }

  //get queue name

  for (let i = 0; i < data.length; i++) {
    for (let j = 0; j < queueList.length; j++) {
      if (queueList[j].dn === data[i].dst_dn) {
        data[i].dst_dn = queueList[j].display_name;
      }
    }
  }

  // data.map(dataUnit => {
  //   queueList.map(queue => {
  //     if (queue.dn === dataUnit.dst_dn) {
  //       dataUnit.dst_dn = queue.display_name;
  //     }
  //   });
  // });

  const rows = data.map((dataUnit, index) => ({
    num:index+1+(page*rowsPerPage),
    id: index,
    dst_start_time: getNewDate(dataUnit.dst_start_time),
    waiting_time: dataUnit.waiting_time.replace(':', 'h ').replace(':', 'm ') + 's',
    talking:
      dataUnit.talking
        .toString()
        .replace(':', 'h ')
        .replace(':', 'm ') + 's',
    src_display_name: dataUnit.src_display_name,
    dst_dn: dataUnit.dst_dn,
  }));

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const handleDowlandButtonClick = () => {
    setRowsPerPage(responseCount);
    setPage(0);
    setData([]);
    handleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  let currentLanguage = useIntl();

  const columns = [
    {
      field:'num',
      headerName:"#",
      renderCell:params =>(
        <b>{params.value}</b>
      )
    },
    {
      field: 'dst_start_time',
      headerName: <IntlMessages id={'queueanswerreport.date'} />,
      width: 250,
      renderCell: params => (
        <span>
          <DateRangeIcon style={{ verticalAlign: '-5px', color: '#406882' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'waiting_time',
      headerName: <IntlMessages id={'queueanswerreport.waitingtime'} />,
      width: 250,
      renderCell: params => (
        <span>
          <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#DB6B97' }} />{' '}
          <b style={{ color: '#DB6B97' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'talking',
      headerName: <IntlMessages id={'queueanswerreport.talkingtime'} />,
      width: 250,
      renderCell: params => (
        <span>
          <AccessTimeIcon style={{ verticalAlign: '-5px', color: '#DB6B97' }} />{' '}
          <b style={{ color: '#DB6B97' }}>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'src_display_name',
      headerName: <IntlMessages id={'queueanswerreport.caller'} />,
      width: 250,
      renderCell: params => (
        <span>
          <PersonIcon style={{ verticalAlign: '-5px', color: '#F05454' }} /> <b>{params.value}</b>
        </span>
      ),
    },
    {
      field: 'dst_dn',
      headerName: <IntlMessages id={'queueanswerreport.queuenumber'} />,
      width: 250,
      renderCell: params => (
        <span>
          <ContactPhoneIcon style={{ verticalAlign: '-5px', color: '#519259' }} /> <b>{params.value}</b>
        </span>
      ),
    },
  ];
  let curentLanguage = useIntl();

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        {rows.length > 0 && (
          <Button onClick={() => handleDowlandButtonClick()} style={{ backgroundColor: '#3f51b5', color: 'white' }}>
            <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          </Button>
        )}
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description">
          <Box sx={style}>
            {rows.length < 1 && (
              <div style={{ textAlign: 'center' }}>
                <CircularProgress />
                <p>Veriler hazirlanıyor...</p>
              </div>
            )}
            <div style={{ textAlign: 'center' }}>
              {rows.length < 1 && (
                <Button disabled style={{ backgroundColor: 'gray', color: 'white' }}>
                  <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                </Button>
              )}
              {rows.length > 0 && (
                <ExcelFile
                  filename={
                    curentLanguage.locale === 'en-EN'
                      ? 'Queue_answer_report'
                      : currentLanguage.locale === 'tr-TR'
                      ? 'Kuyruk_cevap_raporu'
                      : currentLanguage.locale === 'nl-NL'
                      ? 'Rapport voor antwoord in wachtrij'
                      : currentLanguage.locale === 'de-DE'
                      ? 'Warteschlangen-Antwortbericht'
                      : ''
                  }
                  element={
                    <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                      <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
                    </Button>
                  }>
                  <ExcelSheet data={rows} name="Data">
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'Date'
                          : currentLanguage.locale === 'tr-TR'
                          ? 'Tarih'
                          : currentLanguage.locale === 'nl-NL'
                          ? 'Datum'
                          : currentLanguage.locale === 'de-DE'
                          ? 'Datum'
                          : ''
                      }
                      value="dst_start_time"
                    />
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'Waiting Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Bekleme Süresi Toplamı'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Wachttijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Wartezeit'
                          : ''
                      }
                      value="waiting_time"
                    />
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'Talking Time'
                          : curentLanguage.locale === 'tr-TR'
                          ? 'Konuşma Süresi'
                          : curentLanguage.locale === 'nl-NL'
                          ? 'Gesprekstijd'
                          : curentLanguage.locale === 'de-DE'
                          ? 'Gesprächszeit'
                          : ''
                      }
                      value="talking"
                    />
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'Caller'
                          : currentLanguage.locale === 'tr-TR'
                          ? 'Arayan'
                          : currentLanguage.locale === 'nl-NL'
                          ? 'Beller'
                          : currentLanguage.locale === 'de-DE'
                          ? 'Anruferin'
                          : ''
                      }
                      value="src_display_name"
                    />
                    <ExcelColumn
                      label={
                        currentLanguage.locale === 'en-EN'
                          ? 'Queue Number'
                          : currentLanguage.locale === 'tr-TR'
                          ? 'Kuyruk Numarası'
                          : currentLanguage.locale === 'nl-NL'
                          ? 'Wachtrijnummer'
                          : currentLanguage.locale === 'de-DE'
                          ? 'Warteschlangennummer'
                          : ''
                      }
                      value="dst_dn"
                    />
                  </ExcelSheet>
                </ExcelFile>
              )}
            </div>
          </Box>
        </Modal>

        {/* // <ExcelFile
          //   filename={curentLanguage.locale === 'en-US' ? 'Queue_answer_report' : 'Kuyruk_cevap_raporu'}
          //   element={
          //     <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
          //       <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
          //     </Button>
          //   }>
          //   <ExcelSheet data={rows} name="Data">
          //     <ExcelColumn label={currentLanguage.locale == 'en-US' ? 'Date' : 'Tarih'} value="dst_start_time" />
          //     <ExcelColumn
          //       label={currentLanguage.locale == 'en-US' ? 'Waiting Time' : 'Bekleme Süresi'}
          //       value="waiting_time"
          //     />
          //     <ExcelColumn label={currentLanguage.locale == 'en-US' ? 'Talking Time' : 'Konuşma Süresi'} value="talking" />
          //     <ExcelColumn label={currentLanguage.locale == 'en-US' ? 'Caller' : 'Arayan'} value="src_display_name" />
          //     <ExcelColumn label={currentLanguage.locale == 'en-US' ? 'Queue Number' : 'Kuyruk Numarası'} value="dst_dn" />
          //   </ExcelSheet>
          // </ExcelFile> */}
      </div>
      <Paper>
        <div style={{ height: 400, width: '100%' }}>
          <DataGrid
            rows={rows}
            columns={columns}
            hideFooter
            localeText={localeText}
          />
        </div>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={parseInt(responseCount)}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={rows.length < rowsPerPage || rows.length === 0 ? { disabled: true } : { disabled: false }}
          backIconButtonText={String(rows.length)}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      {/* <TableContainer component={Paper} className={classes.root}>
        <Table className={classes.table} style={{ position: 'sticky' }} aria-label="simple table">
          <TableHead>
            <TableRow>
              {column.map((column, index) => (
                <TableCell key={index} style={{ width: column.width, textAlign: 'center' }}>
                  {column.headerName}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => {
              return (
                <TableRow hover role="checkbox" tabIndex={-1} key={index}>
                  {column.map((column, index) => {
                    const value = row[column.id];

                    return (
                      <TableCell key={index} style={{ textAlign: 'center' }}>
                        {value}{' '}
                      </TableCell>
                    );
                  })}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
        <TablePagination
          rowsPerPageOptions={[10, 20, 50, 100]}
          component="div"
          count={responseCount}
          rowsPerPage={rowsPerPage}
          page={page}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={rows.length < rowsPerPage || rows.length === 0 ? { disabled: true } : { disabled: false }}
          backIconButtonText={String(rows.length)}
          onPageChange={handleChangePage}
          labelRowsPerPage={'Rows Per Page'}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </TableContainer>
          */}
    </>
  );
}
