import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import {
  Card,
  CardActions,
  CardContent,
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Typography,
} from '@material-ui/core';
import { getAgentNumber, getQueue } from 'api/api';
import React, { useEffect, useState } from 'react';

function Filter({ agent, queue, setAgent, setQueue }) {
  const [queues, setQueues] = useState([]);
  const [agents, setAgents] = useState([]);

  useEffect(() => {
    getQueue().then(resp => {
      setQueues(JSON.parse(resp.data.data));
    });

    getAgentNumber().then(resp => {
      setAgents(JSON.parse(resp.data.data));
    });
  }, []);

  const handleChangeQueue = event => {
    setQueue(event.target.value);
  };

  const handleChangeAgent = event => {
    setAgent(event.target.value);
  };

  return (
    <Card>
      <Typography>
        <IntlMessages id={'filters'} />
      </Typography>
      <CardContent>
        <GridContainer>
          <Grid item md={4}>
            <FormControl fullWidth variant="outlined">
              <InputLabel id="agent-select-label">
                <IntlMessages id={'internals.summary.agent'} />
              </InputLabel>
              <Select
                labelId="agent-select-label"
                id="agent-select"
                value={agent}
                onChange={handleChangeAgent}
                label={<IntlMessages id={'internals.summary.agent'} />}>
                <MenuItem value={''}>
                  <IntlMessages id={'all'} />
                </MenuItem>
                {agents.map((agent, index) => (
                  <MenuItem key={index} value={agent.dn}>
                    {agent.dn + '-' + agent.display_name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item md={4}>
            <FormControl fullWidth variant="outlined">
              <InputLabel id="agent-select-label">
                <IntlMessages id={'queue'} />
              </InputLabel>
              <Select
                labelId="agent-select-label"
                id="agent-select"
                value={queue}
                onChange={handleChangeQueue}
                label={<IntlMessages id={'queue'} />}>
                <MenuItem value={''}>
                  <IntlMessages id={'all'} />
                </MenuItem>
                {queues.map((queue, index) => (
                  <MenuItem key={index} value={queue.dn}>
                    {queue.dn + '-' + queue.display_name}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </GridContainer>
      </CardContent>
      <CardActions></CardActions>
    </Card>
  );
}
export default Filter;
