import React, { useState, useEffect } from 'react';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import Filter from './Filter';
import Table from './Table';
import * as api from '../../../api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { CircularProgress } from '@material-ui/core';
import Detail from './Detail';
import { Logout } from 'services/auth/logout';
import { useHistory } from 'react-router';
import dateFormat from 'dateformat';

const MonthlyDetails = () => {
  const [, setCsvData] = useState([]);
  const [response, setResponse] = useState([]);
  const [responseTotal] = useState([]);
  const [selectedDate, setSelectedDate] = useState(
    new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate(),
  );

  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'monthlydetails'} />, isActive: true },
  ];

  let today = new Date();
  let yesterday = new Date();
  yesterday.setDate(today.getDate() - 1);
  const [selectedTime, setSelectedTime] = useState(dateFormat(new Date(),'yyyy-mm-dd'));
  const [totalAgent, setTotalAgent] = useState([]);
  const [totalDid, setTotalDid] = useState([]);

  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  let query = `select * from cl_get_daily_statics_proc('${selectedTime}')`;

  let [loading, setLoading] = useState(false);

  function lastDay(date) {
    return dateFormat(date, 'yyyy-mm-dd');
  }

  let queryTotal = `with recursive cte1 as (
    select * from cl_get_daily_statics_proc('${selectedTime}')
      )
  select 
  'Total Call' as d,
  (sum(totalinbound)) as totalinbound,
  (sum(answeredinb)) as answeredinb,
  (sum(durationinbound)) as durationinbound ,
  (sum(totaloutbound)) as totaloutbound,
  (sum(durationoutbound)) as durationoutbound
  from cte1`;

  const history = useHistory();
  useEffect(() => {
      setLoading(true);
      setSelectedTime(lastDay(selectedTime));
      api
        .getMonthlyDetails({ token: localStorage.getItem('token'), query: query })
        .then(resp => {
          setResponse(JSON.parse(resp.data.data));
          api.getMonthlyTotal({ token: localStorage.getItem('token'), query: queryTotal }).then(resps => {
            setResponse([...JSON.parse(resp.data.data), ...JSON.parse(resps.data.data)]);
            setLoading(false);
          });
        })
        .catch(resp => {
          if (resp.response?.status === 401) {
            Logout();
            history.push('/signin');
          }
        });
    
  }, [query, selectedTime, history, queryTotal]);

  return (
    <PageContainer heading={<IntlMessages id={'monthlydetails'} />} breadcrumbs={breadcrumbs}>
      <Filter
        selectedEndTime={selectedTime}
        setSelectedEndTime={setSelectedTime}
        setOffset={setOffset}
        setPage={setPage}
        setLimit={setLimit}
        setTotalAgent={setTotalAgent}
        setTotalDid={setTotalDid}
        selectedDate={selectedDate}
      />
      {loading && (
        <div style={{ textAlign: 'center', marginTop: '20px' }}>
          <CircularProgress />
        </div>
      )}
        <Table
          page={page}
          setPage={setPage}
          setLimit={setLimit}
          setResponse={setResponse}
          setCsvData={setCsvData}
          limit={limit}
          setOffset={setOffset}
          responseTotal={responseTotal}
          offset={offset}
          response={response}
          setSelectedDate={setSelectedDate}
        />
      <Detail totalAgent={totalAgent} totalDid={totalDid} />
    </PageContainer>
  );
};
export default MonthlyDetails;
